import MinRequest from './MinRequest'
import con from './config/config'

const minRequest = new MinRequest()

// 
minRequest.interceptors.request((request) => {
  return request
})

// 
minRequest.interceptors.response((response) => {
  return response.data
})

// 
minRequest.setConfig((config) => {
  config.baseURL = con.API_SITE
  return config
})

export default {
  apis: {
	  common:{//
	  	sting (data) {return minRequest.post('/gzh/Wxlogin/sting', data)},//
	  	sendCode (data) {return minRequest.post('/gzh/Wxlogin/getCode', data)},//
	  	paySundries (data) {return minRequest.post('/gzh/Pay/paySundries', data)},//
	  },
	  user:{//
	  	getuser (data) {return minRequest.get('/gzh/Wxlogin/getuser', data)},//
	  	setdatum (data) {return minRequest.post('/gzh/User/setdatum', data)},//
	  	getUserData (data) {return minRequest.post('/gzh/Personal/getUserData', data)},//
	  	updPayPsw (data) {return minRequest.post('/gzh/Personal/updPayPsw', data)},//
	  	updPhone (data) {return minRequest.post('/gzh/Personal/updPhone', data)},//
	  	friends (data) {return minRequest.post('/gzh/Personal/friends', data)},//
	  	getUp (data) {return minRequest.post('/gzh/Admission/getUp', data)},//
	  },
	  index:{//
	  	classify (data) {return minRequest.post('/gzh/Index/classify', data)},//
	  	carousel (data) {return minRequest.post('/gzh/Index/carousel', data)},//
	  },
	  account:{//
	  	shouPay (data) {return minRequest.post('/gzh/Personal/shouPay', data)},//
	  	addPay (data) {return minRequest.post('/gzh/Personal/addPay', data)},//
	  	billList (data) {return minRequest.post('/gzh/Bill/billList', data)},//
	  	withdrawal (data) {return minRequest.post('/gzh/Pay/withdrawal', data)},//
	  	tyransfer (data) {return minRequest.post('/gzh/Bill/tyransfer', data)},//
	  	dChon (data) {return minRequest.post('/gzh/Pay/dChon', data)},//
	  	goldIngot (data) {return minRequest.post('/gzh/Bill/goldIngot', data)},//
	  	recommend (data) {return minRequest.post('/gzh/Bill/recommend', data)},//
	  	getbank (data) {return minRequest.post('/gzh/Withdrawal/getbank', data)},//
	  	withdrawallist (data) {return minRequest.post('/gzh/Withdrawal/withdrawallist', data)},//
	  },
	  product:{//
	  	getGoodsList (data) {return minRequest.post('/gzh/Goods/getGoodsList', data)},//
	  	getGoods (data) {return minRequest.post('/gzh/Goods/getGoods', data)},//
	  	rush (data) {return minRequest.post('/gzh/Purchase/rush', data)},//
		getTime (data) {return minRequest.post('/gzh/Refresh/getTime', data)},// 
		
	  },
	  shop:{//
	  	getGoodsList (data) {return minRequest.post('/gzh/Goldbalancegoods/goldindex', data)},//
	  	getGoods (data) {return minRequest.post('/gzh/Goldbalancegoods/getgoldGoods', data)},//
	  	rush (data) {return minRequest.post('/gzh/Pay/getgPay', data)},//
	  },
	  card:{//
	  	gradeList (data) {return minRequest.post('/gzh/Admission/admissionList', data)},//
	  },
	  address:{//
	  	gradeList (data) {return minRequest.post('/gzh/Address/getAddress', data)},//
	  	addAddress (data) {return minRequest.post('/gzh/Address/addAddress', data)},//
	  	delAddress (data) {return minRequest.post('/gzh/Address/delAddress', data)},//
	  	defaulst (data) {return minRequest.post('/gzh/Address/defaulst', data)},//
	  },
	  news:{//
	  	article (data) {return minRequest.post('/gzh/index/article', data)},//
	  	articleDetails (data) {return minRequest.post('/gzh/index/articleDetails', data)},//
	  	msg (data) {return minRequest.post('/gzh/index/msg', data)},//
	  	msgDetails (data) {return minRequest.post('/gzh/index/msgDetails', data)},//
	  },
	  order:{//
	  	orderlist (data) {return minRequest.post('/gzh/Order/orderlist', data)},//
	  	physicalOrder (data) {return minRequest.post('/gzh/Order/physicalOrder', data)},//
	  	particulars (data) {return minRequest.post('/gzh/Order/particulars', data)},//
	  	setPayDocument (data) {return minRequest.post('/gzh/Pay/setPayDocument', data)},//
	  	delOrder (data) {return minRequest.post('/gzh/Order/delOrder', data)},//
	  	resell (data) {return minRequest.post('/gzh/Order/resell', data)},//
	  	transfer (data) {return minRequest.post('/gzh/Pay/transfer', data)},//
	  	substitution (data) {return minRequest.post('/gzh/Order/substitution', data)},//
	  	receivingGoods (data) {return minRequest.post('/gzh/Order/receivingGoods', data)},//
	  }
  }
}

